export class TagRead {
  
  constructor(obj) {
    this.tagReadId = obj.TagReadId;
    this.antennaPort = obj.antennaPort;
    this.dateTime = obj.dateTime;
    this.epc = obj.epc;
    this.firstSeenTimestamp = obj.firstSeenTimestamp;
    this.isHeartBeat = obj.isHeartBeat;
    this.peakRssi = obj.peakRssi;
    this.readerId = obj.readerId;
    this.readerName = obj.readerName;
    this.userFamilyName = obj.userFamilyName;
    this.userGivenName = obj.userGivenName;
    this.userId = obj.userId;
    
    this.userFullName = this.userGivenName + ' ' + this.userFamilyName; 
  }
  
}


