<template>
  <div id="user-stats">
    <!-- <div class="component-header-wrapper">
      <h1 class="title is-4 is-marginless has-text-centered">Statistik</h1>
    </div> -->
    <div class="square-content" v-if="userData">
      <div class="box" v-if="!isEmpty">
        <article class="media">
          <div class="media-left">
            <figure class="image is-64x64">
              <img src="http://bulma.io/images/placeholders/128x128.png" alt="Image">
            </figure>
          </div>
          <div class="media-content">
            <div class="content">
              <p>
                <strong>{{ userData.fullName }}</strong> <small>@{{ userData.familyName }}</small>
                <br>
                Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aenean efficitur sit amet massa fringilla egestas. Nullam condimentum luctus turpis.
              </p>
            </div>
          </div>
        </article>
      </div>
      <table class="table is-fullwidth" v-if="!isEmpty">
        <thead></thead>
        <tbody>
          <tr>
            <th></th>
            <th>Idag</th>
            <th>Totalt</th>
          </tr>
          <tr>
            <td>Distans</td>
            <td>{{userData.distanceToday}} m</td>
            <td>{{userData.distanceTotal}} m</td>
          </tr>
          <tr>
            <td>Medelhastighet</td>
            <td>{{userData.averageSpeedKmPHourToday}} km/h</td>
            <td>{{userData.averageSpeedKmPHourTotal}} km/h</td>
          </tr>
          <tr>
            <td>Snitt-tid per km</td>
            <td>{{userData.averageSpeedMinPerKmToday}} min/km</td>
            <td>{{userData.averageSpeedMinPerKmTotal}} min/km</td>
          </tr>
          <tr>
            <td>Uppför</td>
            <td>{{userData.uphillToday}} min/km</td>
            <td>{{userData.uphillTotal}} min/km</td>
          </tr>
        </tbody>
      </table>
      <section class="section" v-if="isEmpty">
          <div class="content has-text-grey has-text-centered">
              <p>Välj en åkare för att visa statistik.</p>
          </div>
      </section>
    </div>
  </div>  
</template>
<script>
import { UserStats } from '../models/user-stats.model.js';

export default {
  name: "",
  data: function() {
    return {
      userData: {},
      asd: [],
    }
  },
  props: ['selectedUserId'],
  watch: {
    selectedUserId(value) {
      this.fetchUserData(value);
    }
  },
  methods: {
    fetchUserData(userId) {
      return this.$http.get(`Users/${userId}/statistics`)
        .then(response => {
          return response.json();
        })
        .then(data => {
          var tempObj = new UserStats(data);
          this.userData = Object.assign({}, tempObj);
          this.asd.push(this.userData)
        })
    }
  },
  computed: {
    isEmpty() {
      return Object.keys(this.userData).length < 1
    }
  },
  created() {
    if (this.selectedUserId) {
      this.fetchUserData(this.selectedUserId);
    }
    
  }
}
</script>
<style lang="scss" scoped>
</style>


