<template>
  <div id="segment-counter">
    <!-- <div class="component-header-wrapper">
      <h1 class="title is-4 is-marginless has-text-centered">Passager
        <a class="button is-pulled-right refresh-button" @click="fetchReadersData">
          <span class="icon">
            <i class="fa fa-refresh" aria-hidden="true"></i>
          </span>
        </a>
      </h1>
    </div> -->
    <div>
      <!-- <a class="button is-pulled-right refresh-button" @click="fetchReadersData">
        <span class="icon">
          <i class="fa fa-refresh" aria-hidden="true"></i>
        </span>
      </a> -->
      <span class="icon is-pulled-right refresh-button" @click="fetchReadersData">
        <i class="fa fa-refresh" aria-hidden="true"></i>
      </span>
    </div>
    <div class="square-content">
      <b-table 
        :data="readersStatistics"
        detailed
        @details-open>
        
        <template scope="props">
          <b-table-column label="Station">
            {{ props.row.name }}
          </b-table-column>
          <b-table-column label="Antal passager idag">
            {{ props.row.totalPassagesToday }}
          </b-table-column>
          <b-table-column label="Pistad senast">
            {{ props.row.latestGroomingTime }}
          </b-table-column>
        </template>
        
        <template slot="detail" scope="props">
          <div class="media-content">
            <div class="content">
                <p>
                    <strong>{{ props.row.name }}</strong>
                    <small>Passager idag</small>
                    <br>
                    <br>
                    <table class="histogram">
                      <tbody>
                        <tr class="data-row">
                          <td v-for="(value, key) in props.row.passagesPerHourToday" :title="value">
                            <div class="data-filler" :style="{'height': (value/props.row.todaysHighestPassagesCount) * 100 + '%'}">
                            </div>
                          </td>
                        </tr>
                        <tr class="label-row">
                          <td v-for="(value, key) in props.row.passagesPerHourToday">
                            {{ key | leadingZero }}
                          </td>
                        </tr>
                      </tbody>
                    </table>
                </p>
            </div>
          </div>
        </template>
      </b-table>
    </div>
  </div>
</template>
<script>
import { ReaderStat } from '../models/reader-stat.model.js';

export default {
  name: "SegmentCounter",
  data: () => ({
    readersStatistics: []
  }),
  methods: {
    fetchReadersData() {
      this.readersStatistics = [];
      return this.$http.get('Readers/statistics')
        .then(response => {
          return response.json();
        })
        .then(data => {
          data.forEach(item => {
            var readerStat = new ReaderStat(item);
            this.readersStatistics.push(readerStat);
          })
        })
    }
  },
  filters: {
    leadingZero(value) {
      if (value < 10) {
        return 0 + value;
      } else {
        return value;
      }
    }
  },
  created() {
    this.fetchReadersData();
  }
}
</script>
<style lang="scss" scoped>
#segment-counter {
  overflow: auto;
  max-height: 100%;
}

.refresh-button {
  cursor: pointer;
  margin-right: 30px;
}

.histogram {
  
  .data-row {
    td {
      height: 50px; 
      padding: 5px 5px 0px; 
      vertical-align: bottom;
      box-shadow: none;
      
      .data-filler {
        background-color: rgb(8, 131, 232);
      }
    }
  }
  .label-row {
    td {
      padding: 0;
      text-align: center;
      box-shadow: none;
    }
  }
}
</style>


