<template>
  <div id="segment">
    <a class="anchor" id="segment-anchor"></a>
    <div class="component-header-wrapper">
      <h1 class="title component-title has-text-centered is-marginless">Segment</h1>
      <p class="subtitle has-text-centered is-marginless">10 senaste</p>
      <div class="dropdown is-right" :class="{'is-active': openDropdown}" @click="openDropdown = !openDropdown">
        <div class="dropdown-trigger">
          <button class="button is-info" aria-haspopup="true" aria-controls="dropdown-menu6">
            <span>{{ currentSegmentName }}</span>
            <span class="icon is-small">
              <i class="fa fa-angle-down" aria-hidden="true"></i>
            </span>
          </button>
        </div>
        <div class="dropdown-menu" id="dropdown-menu6" role="menu">
          <div class="dropdown-content">
            <a class="dropdown-item" @click="currentSegmentId = ''; showAllSegments = true">Alla segment</a>
            <a class="dropdown-item" v-for="segment in segments" @click="currentSegmentId = segment.id; showAllSegments = false">{{ segment.name }}</a>
          </div>
        </div>
      </div>
    </div>
    <div class="square-content">
      <table class="table table-striped is-fullwidth">
        <thead>
          <tr>
            <th>Åkare</th>
            <th>Segment</th>
            <th>Distans</th>
            <th>Senaste varvtid</th>
            <th>Hastighet</th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="data in completedSegments" :key="data.segmentResultId" v-if="showAllSegments || data.segmentId === currentSegmentId" @click="selectUser(data.userId)">
            <td>{{ data.fullName }}</td>
            <td>{{ data.segmentName }}</td>
            <td>{{ data.distance + ' m' }}</td>
            <td>{{ data.duration }}</td>
            <td>{{ data.speedKmPHour + ' km/h' }}</td>
            <td>{{ data.speedMinPerKm + ' min/km' }}</td>
            <td v-if="data.endReadUri" @click="openModal = !openModal"><i class="fa fa-television" aria-hidden="true"></i></td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="modal" :class="{'is-active': openModal}" v-if="openModal">
      <div class="modal-background"></div>
      <div class="modal-content">
        <iframe class="modal-video" allowfullscreen frameborder="0" src="https://www.youtube.com/embed/v1_49OFYwR8"></iframe>
      </div>
      <button class="modal-close is-large" aria-label="close" @click="openModal = !openModal"></button>
    </div>
  </div>
</template>
<script>
export default {
  name: "segment",
  data: () => ({
    openModal: false,
    currentSegmentId: '',
    openDropdown: false,
    showAllSegments: true,
    segments: []
  }),
  props: ['segment', 'completedSegments'],
  computed: {
    currentSegmentName() {
      var currentSegment = 'Alla segment';
      var currentSegmentId = this.currentSegmentId;
      this.segments.forEach(segment => {
        if (segment.id === currentSegmentId) {
          currentSegment = segment.name;
        }
      })
      return currentSegment;
    }
  },
  methods: {
    fetchSegments() {
      this.$http.get('Segments')
        .then(response => {
          return response.json();
        })
        .then(data => {
          this.segments = data;
        })
    },
    selectUser(id) {
      this.$emit('userWasSelected', id);
      console.log(id);
    }
  },
  created() {
    this.fetchSegments();
  }
}
</script>
<style lang="scss" scoped>
#segment {
  overflow: auto;
  max-height: 100%;
  min-height: 100%;

  .dropdown {
    position: absolute;
    right: 1.5rem;
    bottom: 0.5rem;
  }
  
  .modal {
    z-index: 999;
    
    .modal-content {
      background-color: #fff;
      
      .modal-video {
        width: 640px;
        height: 315px;
        display: block;
      }
    }
  }
  
  .table {
    
    tbody {
      tr {
        cursor: pointer;
      }
    }
  }
}
</style>


