<template>
  <div id="details">
    <a class="anchor" id="details-anchor"></a>
    <div class="component-header-wrapper">
      <h1 class="title component-title has-text-centered is-marginless">Stats</h1>
      <div class="tabs is-centered is-boxed">
        <ul>
          <li :class="{'is-active': selectedComponent === 'appSegmentCounter'}" @click="selectedComponent = 'appSegmentCounter'"><a>Passager idag</a></li>
          <li :class="{'is-active': selectedComponent === 'appUserStats'}" @click="selectedComponent = 'appUserStats'"><a>Statistik</a></li>
        </ul>
      </div>
    </div>
    <component :is="selectedComponent" :selectedUserId="selectedUserId"></component>
  </div>
</template>
<script>
import SegmentCounter from './SegmentCounter.vue'
import UserStats from './UserStats.vue'

export default {
  name: "",
  props: {
    selectedUserId: {
      type: String
    },
    selComp: {
      type: String
    }
  },
  data: function() {
    return {
      selectedComponent: 'appSegmentCounter'
    }  
  },
  watch: {
    selectedUserId(value) {
      this.selectedComponent = 'appUserStats';
    }
  },
  components: {
    appSegmentCounter: SegmentCounter,
    appUserStats: UserStats
  }
}
</script>
<style lang="scss" scoped>
#details {
  overflow: auto;
  max-height: 100%;
}

