<template>
  <div id="app" v-resize="onResize">
    <transition name="slide" mode="out-in">
        <app-header v-if="showHeader"></app-header>  
    </transition>

    <div class="div-group" :style="{height: windowSizes.top.height + unit}">
      <div class="square top-left" :style="{width: windowSizes.top.leftWidth + unit}">
        <div class="resizable-border-vertical" @mousedown="initDrag('top', $event)"></div>
        <app-map :readers="readers" v-if="readers.length > 0"></app-map>
      </div>
      <div class="square top-right" :style="{width: windowSizes.top.rightWidth + unit}">
          <app-weather></app-weather>
      </div>
      <div class="resizable-border-horizontal" @mousedown="initDrag('horizontal', $event)"></div>
    </div>
    
    <div class="div-group" :style="{height: windowSizes.bottom.height + unit}">
      <div class="square bottom-left" :style="{width: windowSizes.bottom.leftWidth + unit}">
        <div class="resizable-border-vertical" @mousedown="initDrag('bottom', $event)"></div>
        <app-segment :completedSegments="completedSegments" @userWasSelected="selectedUserId = $event; showUserStats()"></app-segment>
      </div>
      <div class="square bottom-right" :style="{width: windowSizes.bottom.rightWidth + unit}">
        <app-details :selectedUserId="selectedUserId" ref="details"></app-details>
      </div>  
    </div>
  </div>
</template>

<script>
import resize from 'vue-resize-directive'
import Map from './components/Map.vue'
import SegmentCounter from './components/SegmentCounter.vue'
import Details from './components/Details.vue'
import Segment from './components/Segment.vue'
import Weather from './components/Weather.vue'
import Header from './components/Header.vue'
import { CompletedSegment } from './models/completed-segment.model.js';
import { TagRead } from './models/tag-read.model.js';

export default {
  name: 'app',
  components: {
    'app-map': Map,
    'app-details': Details,
    'app-segment': Segment,
    'app-weather': Weather,
    'app-header': Header
  },
  data () {
    return {
      windowSizes: {
        top: {
          leftWidth: 50,
          rightWidth: 50,
          height: 50
        },
        bottom: {
          leftWidth: 50,
          rightWidth: 50,
          height: 50
        }
      },
      unit: '%',
      startWidth: 0,
      startHeight: 0,
      startX: 0,
      startY: 0,
      pos: '',
      showHeader: false,
      completedSegments: [],
      segments: {},
      readers: [],
      stationRead: {},
      selectedUserId: '',
    }
  },
  methods: {
    // Called when mouse is pressed down on a resizable border
    initDrag(pos, e) {
      this.pos = pos;
      this.startX = e.clientX;
      this.startY = e.clientY;
      this.startWidth = e.target.parentElement.clientWidth;
      this.startHeight = e.target.parentElement.style.height;
      document.addEventListener('mousemove', this.doDrag, false);
      document.addEventListener('mouseup', this.stopDrag, false);
    },
    doDrag(e) {
      var windowWidth = window.innerWidth;
      var windowHeight = window.innerHeight;
      
      var newLeftWidth = (e.x / windowWidth) * 100;
      var newRightWidth = 100 - newLeftWidth;
      
      var newTopHeight = (e.y / windowHeight) * 100;
      var newBottomHeight = 100 - newTopHeight;
      
      if (this.pos === 'top') {
        this.windowSizes.top.leftWidth = newLeftWidth;
        this.windowSizes.top.rightWidth = newRightWidth;
      } else if (this.pos === 'bottom') {
        this.windowSizes.bottom.leftWidth = newLeftWidth;
        this.windowSizes.bottom.rightWidth = newRightWidth;
      } else if (this.pos === 'horizontal') {
        this.windowSizes.top.height = newTopHeight;
        this.windowSizes.bottom.height = newBottomHeight;
      }
      this.$ls.set('windowSizes', JSON.stringify(this.windowSizes));
    },
    stopDrag(e) {
      document.removeEventListener('mousemove', this.doDrag, false)
      document.removeEventListener('mouseup', this.stopDrag, false)
    },
    onResize(el) {
      var appWidth = el.offsetWidth;
      
      if (appWidth > 1600) {
        this.showHeader = false;
      } else {
        this.showHeader = true;
      }
    },
    initSignalR() {
      var connection = $.hubConnection();
      var hub = connection.createHubProxy("statisticsHub");
      connection.url = 'http://ostersundskistadium.azurewebsites.net/signalr';
      var vm = this;
      
      hub.on("tagRead", function(tagRead) {
        // for now this will only result in the sensor being colored red and scaled up
        var tagRead = new TagRead(tagRead);
        for (var i = 0; i < vm.readers.length; i++) {
          if (vm.readers[i].id === tagRead.readerId) {
            var index = i;
            $('#reader-' + tagRead.readerId).addClass('pulse');
            setTimeout(() => {
              $('#reader-' + tagRead.readerId).removeClass('pulse');
            }, 500, index);
          }
        }
      });
      
      hub.on("segmentCompleted", function(segmentCompleted) {
        var completedSegment = new CompletedSegment(segmentCompleted);
        
        // only show the 10 latest values
        if (vm.completedSegments.length < 10) {
          vm.completedSegments.unshift(completedSegment);
        } else {
          vm.completedSegments.pop(vm.completedSegments.length);
          vm.completedSegments.unshift(completedSegment);
        }
      });
      
      connection.start()
        .done(() => {
          console.log("Now connected, connection ID=" + connection.id);
        })
        .fail(() => {
          console.log("Could not connect");
        });
    },
    fetchReaders() {
      return this.$http.get('Readers')
        .then(response => {
          return response.json();
        })
        .then(data => {
          this.readers = data;
        })
    },
    showUserStats() {
      this.$refs.details.selectedComponent = 'appUserStats';
    },
    initUI() {
      var windowWidth = window.innerWidth;
      if (windowWidth > 1600) {
        this.showHeader = false;
      } else {
        this.showHeader = true;
      }
    }
  },
  directives: {
    resize
  },
  created() {
    var defaultSizes = JSON.stringify({
      top: {
        leftWidth: 50,
        rightWidth: 50,
        height: 50
      },
      bottom: {
        leftWidth: 50,
        rightWidth: 50,
        height: 50
      }
    });
    this.windowSizes = JSON.parse(this.$ls.get('windowSizes', defaultSizes));
    
    this.fetchReaders()
      .then(() => {
        this.initSignalR();
      });
  },
  mounted() {
    this.initUI();
  }
}
</script>

<style lang="scss">
@import "~bulma";
@import "~buefy/src/scss/buefy";

* {
  box-sizing: border-box;
}

html {
  font-size: 100%;
}

html,
body {
  width: 100%;
  height: 100%;
}

body {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  padding: 0;
  margin: 0;
  font-weight: normal;
}
#app {
  height: 100%;
  width: 100%;
}

.square {
  background-color: $secondary-color;
}

.anchor {
  display: block;
  position: relative;
  visibility: hidden;
  top: -4.0rem;
}

.title {
  padding: 1rem 0;
  background-color: $primary-color;
  color: #fff;
  margin: 0;
  font-weight: 200;
}

.subtitle {
  background-color: $primary-color;
  color: #fff;
  padding-bottom: 0.5rem;
  font-weight: 200;
}

.component-header-wrapper {
  position: relative;
}

.square-content {
  padding: 2.0rem;
  margin: 2.0rem;
  border-radius: 0.3rem;
  box-shadow: 0 0.1rem 0 0 rgba(0,0,0,0.04);
  border-bottom: 0.1rem solid #e2e2e2;
  background-color: #fff;
  overflow-x: auto;
}

@media (max-width: 1299px) {
  
  #app {
    padding-top: 4.0rem;
    transition: padding-top 0.5s ease-out;
  }
  
  .div-group {
    width: 100%;
    height: 200% !important;
  }

  .square {
    width: 100% !important;
    height: 50% !important;
  }
}

@media (min-width: 1300px) and (max-width: 1599px) {

  #app {
    padding-top: 4.0rem;
    transition: padding-top 0.5s ease-out;
  }
  
  .div-group {
    width: 100%;
    height: 100% !important;
    overflow-x: hidden;
  }
  
  .square {
    float: left;
    position: relative;
    display: inline-block;
    width:100%;
    height: 100%;
  }
  
  .resizable-border-vertical {
    width: 4px;
    height: 100%;
    background-color: $resizable-border-color;
    position: absolute;
    right: -2px;
    z-index: 99;
    cursor: ew-resize;
  }

}

@media screen and (min-width: 1600px) {
  
  .div-group {
    width: 100%;
    height: 50%;
    position: relative;
    overflow: hidden;
  }
  
  .square {
    float: left;
    position: relative;
    display: inline-block;
    width: 50%;
    height: 100%;
  }
  
  .resizable-border-horizontal {
    height: 6px;
    width: 100%;
    z-index: 99;
    cursor: ns-resize;
    position: absolute;
    bottom: -3px;
    // background-color: rgba(#585858, 0.7);
    background-color: #fff;
    box-shadow: 0px 0px 6px #000;
  }
  
  .resizable-border-vertical {
      width: 2px;
      height: 100%;
      // background-color: rgba(#585858, 0.7);
      box-shadow: 0px 0px 6px #000;
      background-color: #fff;
      position: absolute;
      right: -1px;
      z-index: 99;
      cursor: ew-resize;
  }
}

.slide-enter {
  opacity: 0;
}

.slide-enter-active {
  animation: slide-in 0.5s ease-out forwards;
  position: absolute;
  transition: opacity .5s;
}

.slide-leave-active {
  animation: slide-out 1s ease-out forwards;
  transition: opacity .5s;
  opacity: 0;
  position: absolute;
}

.slide-move {
  transition: transform 1s;
}

@keyframes slide-in {
  from {
    transform: translateY(-40px);
  }
  to {
    transform: translateY(0);
  }
}

@keyframes slide-out {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(-40px);
  }
}

</style>



