<template>
  <div id="map">
    <a class="anchor" id="map-anchor"></a>
    <h1 class="title has-text-centered is-marginless">Karta</h1>
    <div class="image-wrapper">
      <img class="map-img" src="../assets/map.png" alt="">
      <div class="icon" :id="'reader-' + reader.id" v-for="reader in readers" ref="reader" :title="reader.name"></div>
    </div>
  </div>
</template>
<script>
export default {
  name: "map",
  props: ['readers'],
  data: () => ({
  }),
  mounted() {
    console.log("reader", this.$refs.reader);
    var readers = this.$refs.reader;
    for (var i = 0; i < readers.length; i++) {
      if (i === 0) {
        readers[i].style.top = 41 + '%';
        readers[i].style.left = 52 + '%';
        readers[i].style.backgroundColor = 'green';
      } else {
        readers[i].style.top = 60 + '%';
        readers[i].style.left = 56 + '%';
        readers[i].style.backgroundColor = 'lightgreen';
      }
    }
  }
}
</script>
<style lang="scss" scoped>
#map {
  overflow: auto;
  max-height: 100%;
}

.image-wrapper {
  display: inline-block;
  position: relative;
  
  @media (max-width: 1299px) {
    display: block;
  }
  
  .map-img {
    max-width: 100%;
    max-height: 100%;
    position: relative;
    
    @media (max-width: 1299px) {
      width: 100%;
    }
  }

  .icon {
    height: 36px;
    width: 36px;
    position: absolute;
    border-radius: 50%;
    transition: all 0.2s ease;
  }
  
  .pulse {
    background-color: red !important;
    animation: pulse 0.5s ease;
  }
}

@keyframes pulse {
  0% {
    transform: scale(1);
  },
  75% {
    transform: scale(1.5);
  },
  100% {
    transform: scale(1);
  }
}
</style>


